/* flip card start */
.flip-card {
  background-color: transparent;
  width: 300px;
  height: 450px;
  transform: translateY(-20%);
  perspective: 1000px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  border-radius: 15px;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  backface-visibility: hidden;
  -moz-backface-visibility: hidden;
}

.flip-card:focus {
  outline: 0;
}

.flip-card:hover .flip-card-inner,
.flip-card:focus .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front2,
.flip-card-front3,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
}

.flip-card-front2 {
  border-radius: 15px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  color: white;
  z-index: 2;
  background-color: #ffc107;
}
.flip-card-front2 > img {
  width: 90%;
  margin-top: 50px;
  border-radius: 0px 50px 0px 50px;
}
.flip-card-front2 > h4 {
  font-family: "arial-bold";
  font-size: 20px;
  margin-top: 37px;
  line-height: 26px;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #000;
}
.flip-card-front2 > h3 {
  position: absolute;
  bottom: 12%;
  color: #000;
  font-family: "arial-bold";
  box-shadow: 0px 0px 4px 0px #ccc;
  text-transform: uppercase;
  font-size: 13px;
  transform: translate(-50%, -50%);
  left: 50%;
  border-radius: 15px;
  padding: 10px 37px;
  background-color: #fed501;
}

.flip-card-front3 {
  border-radius: 15px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  color: white;
  z-index: 2;
  background-color: #ffc107;
}
.flip-card-front3 > img {
  width: 90%;
  margin-top: 50px;
