  font-size: 13px;
  transform: translate(-50%, -50%);
  left: 50%;
  border-radius: 15px;
  padding: 10px 37px;
  background-color: #fed501;
}

.flip-card-back {
  background: #fff;
  color: #000;
  border-radius: 15px;
  transform: rotateY(180deg);
  z-index: 1;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.flip-card-back::after {
  position: absolute;
  content: "";
  bottom: 7%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 10px;
  width: 66px;
  height: 5px;
  background-color: #fed501;
}

.flip-card-back > h3 {
